<template>
  <div class="home">
    <div class="appHomeHead">
      <img class="back" v-if="this.$store.state.VisitorShowVal == '2'" @click="ShowBtn" src="@/assets/phone/AppVisitorHome/back.png" alt />
      <div class="HeadText">自然保护区</div>
      <img @click="$router.push('/my')" src="@/assets/phone/APP_activity/my.png" alt />
    </div>
    <div class="ItemTop" v-show="type == '/reserve'">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="item in WalkingLantern" :key="item.id">
          <img :src="item.img" alt />
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="ItemTop" v-show="type == '/VisitorAppointment'">
      <img src="@/assets/phone/AppVisitorHome/Visitor_appointment.png" alt />
    </div>
    <div class="ItemTop" v-show="type == '/VolunteerRecruitment'">
      <img src="@/assets/phone/AppVisitorHome/bg3.png" alt />
    </div>
    <div class="homeGo">
      <router-link :to="{name:'reserve'}">保护区介绍</router-link>
      <router-link :to="{name:'activitylist'}">科普活动</router-link>
      <router-link :to="{name:'VisitorAppointment'}">访客预约</router-link>
      <router-link :to="{name:'VolunteerRecruitment'}">志愿者招募</router-link>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      type: "/reserve",
      WalkingLantern: [
        {
          img: require("@/assets/phone/AppVisitorHome/bg2.png"),
          id: 1,
        },
        {
          img: require("@/assets/phone/AppVisitorHome/bg2.png"),
          id: 2,
        },
        {
          img: require("@/assets/phone/AppVisitorHome/bg2.png"),
          id: 3,
        },
      ],
    };
  },
  methods: {
    ShowBtn() {
      this.$store.commit("SetVisitorShowVal", {
        VisitorShowVal: "1",
      });
    },
  },
  updated() {
    let _path = this.$route.matched[1].path;
    if (_path == "/reserve" || _path == "/activitylist") {
      this.type = "/reserve";
    } else if (_path == "/VisitorAppointment") {
      this.type = "/VisitorAppointment";
    } else if (_path == "/VolunteerRecruitment") {
      this.type = "/VolunteerRecruitment";
    }
  },
  mounted() {
    // console.log(this.$route);
  },
};
</script>

<style lang="scss" scoped>
.home {
  background-color: #f6f6f6;
  width: 100vw;
  height: 100vh;
  font-size: 4vw;
  .appHomeHead {
    width: 100%;
    height: 8vh;
    line-height: 8vh;
    text-align: center;
    color: #fff;
    font-size: 4.44vw;
    font-family: MicrosoftYaHei, MicrosoftYaHei-Regular;
    font-weight: 400;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    position: relative;
    .back {
      width: 6vw;
      position: absolute;
      top: 4vw;
      left: 3vw;
    }
    .HeadText{
      font-size: 6.5vw;
    }
    img {
      width: 5vw;
      position: absolute;
      top: 4.5vw;
      right: 3vw;
    }
  }
  .homeGo {
    width: 100vw;
    height: 5vh;
    display: flex;
    justify-content: space-around;
    box-sizing: border-box;
    a {
      height: 4vh;
      text-decoration: none;
      color: #006d5b;
      box-sizing: border-box;
      padding: 0 3vw;
    }
    .router-link-active {
      color: #006d5b;
      padding: 0 3vw;
      border-bottom: 0.5vw solid #006d5b;
    }
  }
  .ItemTop {
    width: 96vw;
    height: 17vh;
    padding: 3vw 0;
    margin: 0 auto;
    img {
      width: 100%;
      height: 30vw;
    }
    .mint-swipe {
      width: 100%;
      height: 30vw;
    }
  }
}
</style>
